import React from 'react'
import './App.css'
import 'jquery'
import 'bootstrap/dist/js/bootstrap'
import 'popper.js/dist/umd/popper'
import 'bootstrap/dist/css/bootstrap.css'
import NavBar from './components/NavBar'
import CustomersList from './components/CustomersList'
import ShoppingCart from './components/ShoppingCart'
import Login from './components/login'
import Dashboard from './components/Dashboard'
import { Router } from 'react-router'
import { Routes, Route, HashRouter } from 'react-router-dom'
import NoMatchPages from './components/NoMatchPages'
import { isConstructorDeclaration } from 'typescript'

import SideBar from './components/SideBar'
import ProductByID from './components/ProductByID'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isLoggedIn: false }
  }
  render() {
    return (
      <HashRouter>
        <NavBar
          isLoggedIn={this.state.isLoggedIn}
          updateIsLoggedInStatus={this.updateIsLoggedInStatus}
        />
        <div className="container-fluid">
          <div className="row">
            <div className="col-lg-3">{this.state.isLoggedIn ? <SideBar /> : ''}</div>
            <div className="col-lg-9">
              <Routes>
                <Route
                  path="/"
                  exact
                  element={
                    <Login
                      {...this.props}
                      updateIsLoggedInStatus={this.updateIsLoggedInStatus}
                    />
                  }
                />
                <Route path="/dashboard" exact element={<Dashboard />} />
                <Route path="/customers" exact element={<CustomersList />} />
                <Route path="/cart" exact element={<ShoppingCart />} />
                <Route path="/product/:id" exact element={<ProductByID />} />
                <Route path="*" element={<NoMatchPages />} />
              </Routes>
            </div>
          </div>
        </div>
      </HashRouter>
    )
  }

  updateIsLoggedInStatus = (status: boolean) => {
    this.setState({ isLoggedIn: status })
  }
}
export default App
